@use '../base/variables' as v;

.card {
  width: 35rem;
  margin: 30rem auto 35rem auto;
  border-radius: 1.5rem;
  overflow: hidden;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-shadow: 0 1rem 4rem rgba($color: #000000, $alpha: .2);

  &_img {
    position: absolute;
  }

  &_profile {
    border: white solid .5rem;
    border-radius: 10rem;
    z-index: 1;
    margin-top: 9rem;
  }

  
  &_name {
    margin-top: 2.5rem;
    font-size: 1.8rem;
  }

  &_lightfont {
    margin-left: .5rem;
    color: v.$dark-grey;
    font-weight: 400;
  }

  &_city {
    margin-top: 1rem;
    font-size: 1.35rem;
    color: v.$dark-grey-blue;
  }

  &_counts {
    margin-top: 2.5rem;
    align-self: stretch;
    border-top: v.$dark-grey 1px solid;
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    padding: 2rem 0;
  }

  &_amount {
    margin-bottom: .5rem;
    font-size: 1.8rem;
  }

  &_data {
    letter-spacing: .2rem;
    color: v.$dark-grey;
  }

}