@use '../base/variables' as v;

.github {
  color: rgba(255, 255, 255, 0.7);
  width: 35rem;
  margin: 2rem auto;
  text-align: center;
  padding: 1.2rem;
  font-weight: 700;
  border-radius: 3rem;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
  z-index: 1;
  border: rgba(255, 255, 255, 0.137) solid 1px;

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    z-index: -1;
    background-color: rgba(0, 0, 0, 0.15);
    backdrop-filter: blur(1rem);
  }
}

.ms_logo {
  padding: 1.3rem;
  margin-left: 1rem;
  background: 
  url('../images/mslogo.svg') 
  center / 1.5rem 1.5rem 
  no-repeat;

  &:hover {
    opacity: .5;
  }
}

@media (max-width: 37rem) {
  body {
    background: 
    url('../images/bg-pattern-top.svg') top -35rem left -80rem no-repeat, 
    url('../images/bg-pattern-bottom.svg') bottom -42rem right -80rem no-repeat,
    v.$dark-cyan;
    }

    .card {
      margin-top: 20rem;
      margin-bottom: 25rem;
    }
  }