@use 'variables' as v;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  color: v.$very-dark-desa-blue;
}

body {
  font-family: 'Kumbh Sans', sans-serif;
  background: 
  url('../images/bg-pattern-top.svg') top -35rem left -5rem no-repeat, 
  url('../images/bg-pattern-bottom.svg') bottom -42rem right 1rem no-repeat, 
  v.$dark-cyan;
}

a:visited, a:link {
  color: hsl(185, 100%, 50%);
} 